<template>
  <el-tabs
    ref="ElTabsDict"
    class="vue-element-dict-tabs"
    v-on="$listeners"
    v-bind="$attrs"
    @tab-click="handdleChange"
  >
    <template
      v-for="(slot, key) in $scopedSlots"
      :slot="key"
      slot-scope="scope"
    >
      <slot :name="key" v-bind="scope"></slot>
    </template>
    <template v-for="(slot, key) in $slots" :slot="key">
      <slot :name="key"></slot>
    </template>
    <el-tab-pane
      v-for="item in list"
      :disabled="item.disabled"
      :key="item[dictConfig.format.value]"
      :name="item[dictConfig.format.value]"
    >
      <!--  -->
      <el-badge
        v-if="badgeObj[item[dictConfig.format.value]]"
        slot="label"
        :type="badgeObj[item[dictConfig.format.value]].type"
        :is-dot="badgeObj[item[dictConfig.format.value]].isDot"
        :value="badgeObj[item[dictConfig.format.value]].value"
        :max="badgeObj[item[dictConfig.format.value]].max"
        :hidden="badgeObj[item[dictConfig.format.value]].hidden"
        class="item"
      >
        {{ item[dictConfig.format.label] }}
      </el-badge>
      <!--  -->
      <template v-else slot="label">{{
        item[dictConfig.format.label]
      }}</template>
    </el-tab-pane>
  </el-tabs>
</template>
<script>
import store from "~/store/index.js"
export default {
  name: "el-tabs-dict",
  data() {
    return {
      list: [], //
      type: "" //
    }
  },
  props: {
    //
    badgeObj: {
      type: Object,
      default: () => {
        return {}
      }
    },
    //
    keyValue: {
      type: Boolean,
      default: false
    },
    dictType: {
      type: [String, Object]
    }, //
    value: {}, //v-model
    //
    disableObj: {
      type: Object
    },
    //
    filterDataFun: {
      type: Function
    },
    //
    disabledDataFun: {
      type: Function
    }
  },
  computed: {
    dictCodes() {
      return store.state.dictCodes
    },
    dictConfig() {
      return store.state.dictConfig
    },
    isGettingAllDictTypes() {
      return store.state.isGettingAllDictTypes
    },
    isGettingDictTypes() {
      return store.state.isGettingDictTypes
    }
  },
  watch: {
    isGettingDictTypes(val) {
      if (!val.includes(this.type)) {
        this.getDictionary_option()
      }
    },
    // 
    isGettingAllDictTypes(val) {
      if (!val) {
        this.getDictionary_option()
      }
    }
  },
  mounted() {
    this.getDictionary_option()
  },
  methods: {
    // 
    async getDictionary_option() {
      if (!this.dictType) {
        return
      }
      //
      let dictData = []
      if (typeof this.dictType === "object") {
        this.type = this.dictType.type
        await store.actions.getDictDataObj(this.type)
        dictData = [].concat(this.dictCodes[this.type])
        const {
          filters,
          filterType = this.dictConfig.format.value,
          reverse = false
        } = this.dictType
        const filtersArr =
          typeof filters === "string" ? filters.split(",") : filters
        if (reverse) {
          dictData = dictData.filter((item) => {
            return !filtersArr.includes(item[filterType])
          })
        } else {
          dictData = dictData.filter((item) => {
            return filtersArr.includes(item[filterType])
          })
        }
      } else {
        this.type = this.dictType
        await store.actions.getDictDataObj(this.type)
        dictData = [].concat(this.dictCodes[this.type])
      }

      //
      if (this.filterDataFun) {
        dictData = this.filterDataFun(dictData)
      }

      //
      if (this.disabledDataFun) {
        dictData = this.disabledDataFun(dictData)
      }

      if (this.disableObj) {
        const {
          disableValue,
          disableType = this.dictConfig.format.value,
          reverse = false
        } = this.disableObj
        const disableList =
          typeof disableValue === "string"
            ? disableValue.split(",")
            : disableValue
        if (reverse) {
          dictData = dictData.map((item) => {
            const targetItem = JSON.parse(JSON.stringify(item))
            targetItem.disabled =
              !disableList.includes(targetItem[disableType]) ||
              targetItem.disabled
            return targetItem
          })
        } else {
          dictData = dictData.map((item) => {
            const targetItem = JSON.parse(JSON.stringify(item))
            targetItem.disabled =
              disableList.includes(targetItem[disableType]) ||
              targetItem.disabled
            return targetItem
          })
        }
      }
      this.list = dictData
    },
    // 
    handdleChange(val) {
      /* emit
         */
      if (!this.keyValue) {
        this.$emit("dictChange", val)
        return
      }
      const item = this.list.find((item) => {
        return item[this.dictConfig.format.value] === val
      })
      this.$emit("dictChange", item)
    },
    getRef() {
      return this.$refs.ElTabsDict
    }
  }
}
</script>
