<template>
  <el-tree ref="ElTreeDict" :data="options" :props="setting">
    <template
      v-for="(slot, key) in $scopedSlots"
      :slot="key"
      slot-scope="scope"
    >
      <slot :name="key" v-bind="scope"></slot>
    </template>
    <template v-for="(slot, key) in $slots" :slot="key">
      <slot :name="key"></slot>
    </template>
  </el-tree>
</template>
<script>
import store from "~/store/index.js"
import {ListToTree} from "~/functions/list-tree"
export default {
  name: "el-tree-dict",
  data() {
    return {
      list: [], //
      type: "" //
    }
  },
  props: {
    //
    maxLevel: {
      type: [Number, String]
    },
    dictType: {
      type: [String, Object],
      default: "",
      required: true
    },
    disableObj: {
      type: Object
    },
    //
    filterDataFun: {
      type: Function
    },
    //
    disabledDataFun: {
      type: Function
    }
  },
  computed: {
    dictCodes() {
      return store.state.dictCodes
    },
    dictConfig() {
      return store.state.dictConfig
    },
    isGettingAllDictTypes() {
      return store.state.isGettingAllDictTypes
    },
    isGettingDictTypes() {
      return store.state.isGettingDictTypes
    },
    setting() {
      const props = this.$attrs.props??{}
      props.label = props.label??this.dictConfig.treeSetting.labelField
      props.children = props.children??this.dictConfig.treeSetting.childrenField
      props.isLeaf = props.leaf??this.dictConfig.treeSetting.leafField
      return props
    },
    options() {
      const props = this.$attrs.props??{}
      const newProps = {
        labelField: props.label??this.dictConfig.treeSetting.labelField,
        childrenField: props.children??this.dictConfig.treeSetting.childrenField,
        leafField: props.leaf??this.dictConfig.treeSetting.leafField
      }
      const treeSetting = Object.assign(this.dictConfig.treeSetting, newProps)
      const treeArr = JSON.parse(JSON.stringify(this.list))
      const data = ListToTree(treeArr, treeSetting)
      // 
      if (this.maxLevel) {
        this.filterLevel(data, +this.maxLevel)
      }
      return data
    }
  },
  watch: {
    isGettingDictTypes(val) {
      if (!val.includes(this.type)) {
        this.getDictionary_option()
      }
    },
    // 
    isGettingAllDictTypes(val) {
      if (!val) {
        this.getDictionary_option()
      }
    }
  },
  mounted() {
    this.getDictionary_option()
  },
  methods: {
    //
    filterLevel(arr, maxLevel) {
      for (let i = 0; i < arr.length; i++) {
        const item = arr[i]
        const currentLevel = item[this.dictConfig.treeSetting.levelField]
        if (currentLevel === maxLevel) {
          delete item[this.setting.children]
          item[this.setting.isLeaf] = true
          continue
        } else {
          this.filterLevel(item[this.setting.children], maxLevel)
        }
      }
    },
    // 
    async getDictionary_option() {
      if (!this.dictType) {
        return
      }
      //
      let dictData = []
      if (typeof this.dictType === "object") {
        this.type = this.dictType.type
        await store.actions.getDictDataObj(this.type)
        dictData = [].concat(this.dictCodes[this.type])
        const {
          filters,
          filterType = this.dictConfig.treeSetting.idField,
          reverse = false
        } = this.dictType
        const filtersArr =
          typeof filters === "string" ? filters.split(",") : filters
        if (reverse) {
          dictData = dictData.filter(item => {
            return !filtersArr.includes(item[filterType])
          })
        } else {
          dictData = dictData.filter(item => {
            return filtersArr.includes(item[filterType])
          })
        }
      } else {
        this.type = this.dictType
        await store.actions.getDictDataObj(this.type)
        dictData = [].concat(this.dictCodes[this.type])
      }

      //
      if (this.filterDataFun) {
        dictData = this.filterDataFun(dictData)
      }

      //
      if (this.disabledDataFun) {
        dictData = this.disabledDataFun(dictData)
      }

      if (this.disableObj) {
        const {
          disableValue,
          disableType = this.dictConfig.treeSetting.idField,
          reverse = false
        } = this.disableObj
        const disableList =
          typeof disableValue === "string"
            ? disableValue.split(",")
            : disableValue
        if (reverse) {
          dictData = dictData.map(item => {
            const targetItem = JSON.parse(JSON.stringify(item))
            targetItem.disabled =
              !disableList.includes(targetItem[disableType]) ||
              targetItem.disabled
            return targetItem
          })
        } else {
          dictData = dictData.map(item => {
            const targetItem = JSON.parse(JSON.stringify(item))
            targetItem.disabled =
              disableList.includes(targetItem[disableType]) ||
              targetItem.disabled
            return targetItem
          })
        }
      }

      this.list = dictData
    },
    // 
    handdleChange(val) {
      /* emit
         */
      this.$emit("dictChange", val)
    },
    getRef() {
      return this.$refs.ElTreeDict
    }
  }
}
</script>